<template>
  <div
    class="canvas_wrapper"
    :style="{
      '--zoom': zoom,
      '--canvas-width': `${canvas.width}px`,
      '--canvas-height': `${canvas.height}px`,
    }"
  >
    <div
      class="canvas"
      :class="{
        'canvas--rounded': rounded,
      }"
      ref="canvas"
    >
      <div
        class="canvas_bg"
        :style="{
          '--background': bannersStore.getBackgroundStyle(
            canvas,
            banner,
            scheme,
          ),
          '--background-with-text': bannersStore.getBackgroundWithTextStyle(
            canvas,
            banner,
            scheme,
          ),
        }"
      ></div>
      <VueDraggableResizable
        v-for="layer in layers"
        :key="layer.id"
        :draggable="editable"
        :resizable="editable"
        :w="bannersStore.getWidth(layer, canvas, banner)"
        :h="bannersStore.getHeight(layer, canvas, banner)"
        :y="bannersStore.getTopOffset(layer, canvas, banner)"
        :x="bannersStore.getLeftOffset(layer, canvas, banner)"
        :minh="0"
        :minw="0"
        :scale="zoom"
        :lockAspectRatio="
          bannersStore.getImageShape(layer, canvas, banner).value === 'circle'
        "
        v-model:active="layer.isActive"
        @resizing="bannersStore.resizeLayer"
        @dragging="bannersStore.positionLayer"
        @activated="bannersStore.activateLayer(layer)"
        @deactivated="bannersStore.deactivateLayer(layer)"
        class="canvas_layer"
        :class="{
          'is-disabled': !editable,
        }"
      >
        <div
          v-if="layer.type === 'image'"
          class="canvas_image"
          :class="{
            'is-circle':
              bannersStore.getImageShape(layer, canvas, banner).value ===
              'circle',
          }"
          :style="{
            '--border-radius': `${bannersStore.getBorderRadius(
              layer,
              canvas,
              banner,
            )}px`,
            '--rotate': `${bannersStore.getRotation(layer, canvas, banner)}deg`,
          }"
        >
          <div
            v-if="!bannersStore.getImage(layer, banner, scheme).url"
            class="canvas_image_placeholder"
          >
            <AppIcon icon="gallery" />
          </div>
          <img
            v-else
            class="canvas_image_picture"
            :src="bannersStore.getImage(layer, banner, scheme).url"
          />
        </div>
        <div
          v-if="layer.type === 'text'"
          class="canvas_text"
          :style="{
            '--font-size': `${bannersStore.getFontSize(
              layer,
              canvas,
              banner,
            )}px`,
            '--font-color': bannersStore.getFontColor(
              layer,
              canvas,
              banner,
              scheme,
            ),
            '--font-family': bannersStore.getFontFamily(layer, canvas, banner)
              .value,
            '--font-weight': bannersStore.getFontWeight(layer, canvas, banner)
              .value,
            '--text-align': bannersStore.getTextAlign(layer, canvas, banner),
            '--text-vertical-align': bannersStore.getTextVerticalAlign(
              layer,
              canvas,
              banner,
            ),
            '--line-height': `${
              bannersStore.getLineHeight(layer, canvas, banner) / 100
            }em`,
            '--letter-spacing': `${
              bannersStore.getLetterSpacing(layer, canvas, banner) / 100
            }em`,
            '--rotate': `${bannersStore.getRotation(layer, canvas, banner)}deg`,
          }"
          @dblclick.prevent.stop="enterTextEditMode"
        >
          <div class="canvas_text_wrapper">
            <div>{{ layer.text }}&nbsp;</div>
            <textarea
              rows="1"
              v-model="layer.text"
              :disabled="!isTextEditMode"
              @blur="isTextEditMode = false"
              @mousedown.stop
              v-if="editable"
            ></textarea>
          </div>
        </div>
      </VueDraggableResizable>
      <BannerCopyright
        :variant="bannersStore.getCopyrightType(canvas, banner, scheme)"
        class="canvas_copyright"
      />
    </div>
    <div ref="preload" class="canvas_preload"></div>
  </div>
</template>

<script>
import BannerCopyright from '@/components/BannerCopyright.vue';
import VueDraggableResizable from '@/components/VueDraggableResizable/vue-draggable-resizable.vue';
import { useAppStore } from '@/stores/useAppStore';
import { useBannersStore } from '@/stores/useBannersStore';
import { useToaster } from '@/utils/useToaster';

export default {
  setup() {
    return {
      appStore: useAppStore(),
      bannersStore: useBannersStore(),
      toaster: useToaster(),
    };
  },
  data: () => ({
    isTextEditMode: false,
  }),
  props: {
    rounded: {
      type: Boolean,
    },
    id: {
      type: String,
    },
    zoom: {
      type: Number,
    },
    editable: {
      type: Boolean,
      default: false,
    },
    banner: {
      type: Object,
      required: true,
    },
    canvas: {
      type: Object,
      required: true,
    },
    scheme: {
      type: String,
    },
  },
  methods: {
    async init() {
      document.addEventListener('keydown', this._keyListener);
    },
    enterTextEditMode($event) {
      if (this.isTextEditMode || !this.editable) return;

      this.isTextEditMode = true;
      const textarea = $event.target
        .closest('.canvas_text')
        .querySelector('textarea');
      this.$nextTick(() => {
        textarea.select();
      });
    },
    _keyListener(e) {
      const {
        key,
        shiftKey,
        target: { tagName: eventSource },
      } = e;
      const isTextInput = eventSource === 'INPUT' || eventSource === 'TEXTAREA';

      switch (key) {
        case 'Escape':
          if (this.activeLayer && !isTextInput) {
            this.bannersStore.deactivateLayer(this.activeLayer);
          }
          break;

        case 'Backspace':
        case 'Delete':
          if (this.activeLayer && !isTextInput) {
            this.bannersStore.deleteLayer(this.activeLayer);
          }
          break;

        case 'ArrowUp':
        case 'ArrowRight':
        case 'ArrowDown':
        case 'ArrowLeft':
          if (this.activeLayer && !isTextInput) {
            e.preventDefault();
            this.bannersStore.shiftLayer(
              this.activeLayer,
              key.slice(5).toLowerCase(),
              shiftKey,
            );
          }
          break;

        default:
          break;
      }
    },
  },
  computed: {
    layers() {
      return this.banner.layers.map((layer) => layer).reverse();
    },
    activeLayer() {
      return this.bannersStore.activeLayer;
    },
  },
  mounted() {
    this.init();
  },
  beforeUnmount() {
    document.removeEventListener('keydown', this._keyListener);
  },
  components: {
    BannerCopyright,
    VueDraggableResizable,
  },
};
</script>

<style lang="scss" scoped>
.canvas_wrapper {
  height: calc(var(--canvas-height) * var(--zoom));
  position: relative;
  display: flex;
  justify-content: center;
  width: var(--canvas-width);
  transform: scale(var(--zoom));
  transform-origin: top center;
  transition: transform 0.135s ease;
}
.canvas {
  width: var(--canvas-width);
  height: var(--canvas-height);
  background: #fff;
  position: relative;

  &--rounded {
    border-radius: 12px;
  }
}
.canvas_bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background: var(--background) center / cover no-repeat;
  border-radius: inherit;

  &::before {
    width: 100%;
    height: 100%;
    background: var(--background-with-text) center / cover no-repeat;
    display: block;
    content: '';
    opacity: 0.5;
    border-radius: inherit;
  }
  .canvas_wrapper.is-saving &::before {
    display: none;
  }
}
.canvas_layer {
  &.is-disabled {
    outline: none;
  }
}
.canvas_image {
  height: 100%;
  width: 100%;
  transform: rotate(var(--rotate));

  &_placeholder {
    height: 100%;
    width: 100%;
    background: #f2f2f2;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: var(--border-radius);
    color: #cfcfcf;

    .icon {
      width: 50%;
      height: 50%;
      max-width: 100px;
      max-height: 100px;
    }
  }
  &_picture {
    width: 100%;
    height: 100%;
    display: block;
    border-radius: var(--border-radius);
    object-fit: cover;
  }
  &.is-circle {
    .canvas_image_placeholder,
    .canvas_image_picture {
      border-radius: 50%;
    }
  }
}
.canvas_text {
  height: 100%;
  display: flex;
  color: var(--font-color);
  justify-content: var(--text-align);
  text-align: var(--text-align);
  align-items: var(--text-vertical-align);
  font-size: var(--font-size);
  font-family: var(--font-family);
  font-weight: var(--font-weight);
  line-height: var(--line-height);
  letter-spacing: var(--letter-spacing);
  transform: rotate(var(--rotate));

  &_wrapper {
    display: grid;

    div,
    textarea {
      color: inherit;
      grid-area: 1/1;
      text-align: inherit;
      width: auto;
      font: inherit;
      letter-spacing: inherit;
      padding: 0;
      margin: 0;
      resize: none;
      background: none;
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      border: none;
      white-space: pre-line;
      word-break: break-word;
      min-height: 1em;
    }
    textarea {
      overflow: hidden;
      color: transparent;
    }
  }
}
.canvas_copyright {
  position: absolute;
  bottom: 20px;
  left: 20px;
  transform: scale(2);
  transform-origin: left bottom;

  .canvas.is-saving & {
    display: none;
  }
}
</style>
