import { defineStore } from 'pinia';
import { nextTick } from 'vue';
import api from '@/api';
import { useCategoriesStore } from '@/stores/useCategoriesStore';
import { isObject } from '@/utils/isObject';
import { nanoid } from 'nanoid';

const categoriesStore = useCategoriesStore();

export const useBannersStore = defineStore('banners', {
  state: () => {
    return {
      banners: {
        data: [],
        status: '',
        error: '',
      },
      banner: {
        data: {
          conversion: '',
          userGroup: null,
          category: null,
          canvases: [
            {
              title: 'Insta Post (Ads)',
              width: 1080,
              height: 1080,
              layers: {},
              schemes: {},
            },
            {
              title: 'Stories (Ads)',
              width: 1080,
              height: 1920,
              layers: {},
              schemes: {},
            },
            {
              title: 'Fb / Youtube / Email',
              width: 1280,
              height: 720,
              layers: {},
              schemes: {},
            },
          ],
          layers: [],
          schemes: [],
          files: [],
        },
        status: '',
        error: '',
      },
      listFontFamily: [
        {
          label: 'Gilroy',
          value: '"Gilroy", sans-serif',
        },
        {
          label: 'Proxima Nova',
          value: '"Proxima Nova", sans-serif',
        },
        {
          label: 'Montserrat',
          value: '"Montserrat", sans-serif',
        },
        {
          label: 'Bebas Neue',
          value: '"Bebas Neue", sans-serif',
        },
        {
          label: 'Microsoft Sans Serif',
          value: '"Microsoft Sans Serif", sans-serif',
        },
        {
          label: 'Nanum Pen',
          value: '"Nanum Pen", cursive',
        },
        {
          label: 'Bodoni',
          value: '"Bodoni", serif',
        },
      ],
      listFontWeight: [
        {
          label: 'Thin',
          value: 100,
        },
        {
          label: 'Extra Light',
          value: 200,
        },
        {
          label: 'Light',
          value: 300,
        },
        {
          label: 'Normal',
          value: 400,
        },
        {
          label: 'Medium',
          value: 500,
        },
        {
          label: 'Semi Bold',
          value: 600,
        },
        {
          label: 'Bold',
          value: 700,
        },
        {
          label: 'Extra Bold',
          value: 800,
        },
        {
          label: 'Black',
          value: 900,
        },
      ],
      listImageShapes: [
        {
          label: 'Прямоугольная',
          value: 'rectangle',
        },
        {
          label: 'Круглая',
          value: 'circle',
        },
      ],
      listUserGroups: [
        {
          label: 'Для всех',
          value: 'all',
        },
        {
          label: 'Для premium',
          value: 'premium',
        },
      ],
      copyrightTypes: ['light', 'transparent', 'dark'],
      currentSchemeId: '',
      currentCanvasId: 0,
      selectedColorScheme: null,
    };
  },

  getters: {
    listCategories() {
      return categoriesStore.categories.data.map(({ id, name }) => {
        return {
          label: name,
          value: id,
        };
      });
    },
    canvases() {
      return this.banner.data.canvases;
    },
    canvas() {
      return this.canvases[this.currentCanvasId];
    },
    schemes() {
      return this.banner.data.schemes;
    },
    layers() {
      return this.banner.data.layers;
    },
    files() {
      return this.banner.data.files;
    },
    downloadsCount() {
      return this.banner.data.used;
    },
    conversion() {
      return this.banner.data.conversion;
    },
    activeLayer() {
      return this.layers.find((layer) => layer.isActive) || {};
    },
  },

  actions: {
    async getBanners() {
      this.banners.status = 'loading';
      try {
        const { data } = await api.banners.get();
        this.banners.data = data.banners.map((banner) => {
          banner.data = {
            conversion: banner.conversion,
            userGroup: banner.is_premium
              ? this.listUserGroups[1]
              : this.listUserGroups[0],
            category: this.listCategories.find(
              (item) => item.value === banner.category_id,
            ),
            schemes: banner.data?.schemes || [],
            canvases: banner.data?.canvases || [],
            layers: banner.data?.layers || [],
            files: banner.files || [],
          };
          return banner;
        });
        this.banners.status = 'success';
        this.banners.error = null;
        return Promise.resolve();
      } catch (e) {
        this.banners.status = 'error';
        this.banners.error = e;
        return Promise.reject(e);
      }
    },
    async getBanner(id) {
      this.banner.status = 'loading';
      try {
        const { data: banner } = await api.banners.get(id);

        // Dumb fix because if backend gets empty {} it transform it to [].
        // But frontend needs object, even if an empty one.
        // If object has some data, then backend saves it properly
        banner.data?.canvases?.forEach((canvas) => {
          if (!isObject(canvas.layers)) {
            canvas.layers = {};
          }
        });

        this.banner.data = {
          used: banner.used,
          conversion: banner.conversion,
          userGroup: banner.is_premium
            ? this.listUserGroups[1]
            : this.listUserGroups[0],
          category: this.listCategories.find(
            (item) => item.value === banner.category_id,
          ),
          schemes: banner.data?.schemes || [],
          canvases: this.prepareCanvases(banner.data?.canvases) || [],
          layers: banner.data?.layers || [],
          files: banner.files || [],
        };
        this.layers.forEach((layer) => {
          if (layer.type === 'text') {
            this.addTextLayerGetterSetter(layer);
          }
        });
        this.currentSchemeId = this.banner.data.schemes[0]?.id;
        this.banner.status = 'success';
        this.banner.error = null;
        return Promise.resolve();
      } catch (e) {
        this.banner.status = 'error';
        this.banner.error = e;
        return Promise.reject(e);
      }
    },
    async createBanner(payload) {
      try {
        const { data } = await api.banners.create(payload);
        return Promise.resolve(data);
      } catch (e) {
        return Promise.reject(e);
      }
    },
    async updateBanner(payload) {
      try {
        const { data } = await api.banners.update(payload);
        return Promise.resolve(data);
      } catch (e) {
        return Promise.reject(e);
      }
    },
    async deleteBanner(id) {
      try {
        const { data } = await api.banners.delete(id);
        return Promise.resolve(data);
      } catch (e) {
        return Promise.reject(e);
      }
    },
    async restoreBanner(id) {
      try {
        const { data } = await api.banners.restore(id);
        return Promise.resolve(data);
      } catch (e) {
        return Promise.reject(e);
      }
    },

    prepareCanvases(canvases) {
      return canvases.map((el) => ({
        ...el,
        checked: false,
      }));
    },

    addTextLayerGetterSetter(layer) {
      Object.defineProperty(layer, 'text', {
        get() {
          return this._text;
        },
        set(value) {
          this._text = value?.slice(0, this.textLimit || undefined);
        },
      });
      Object.defineProperty(layer, 'textLimit', {
        get() {
          return this._textLimit;
        },
        set(value) {
          this._textLimit = value;
          this.text = this._text;
        },
      });
    },
    setCanvasId(id) {
      this.currentCanvasId = id;
    },

    getLayersByTitles(titles) {
      return this.banner.data.layers.reduce((acc, curValue) => {
        if (titles.includes(curValue.title)) {
          acc[curValue.title] = curValue;
        }

        return acc;
      }, {});
    },

    // Get layer properties
    getWidth({ id }, canvas, banner) {
      return (
        canvas.layers[id].width ?? banner.canvases[0].layers[id].width ?? 100
      );
    },
    getHeight({ id }, canvas, banner) {
      return (
        canvas.layers[id].height ?? banner.canvases[0].layers[id].height ?? 100
      );
    },
    getTopOffset({ id }, canvas, banner) {
      return canvas.layers[id].top ?? banner.canvases[0].layers[id].top ?? 10;
    },
    getLeftOffset({ id }, canvas, banner) {
      return (
        canvas.layers[id].left ??
        banner.canvases[0].layers[id].left ??
        banner.canvases[0].width / 2 - 50
      );
    },
    getImage(layer, banner, scheme) {
      const schemeId = scheme || banner.schemes[0].id;
      return this.getFile(layer.schemes[schemeId].image) || {};
    },
    getImageShape({ id }, canvas, banner) {
      return (
        canvas.layers[id].shape ??
        banner.canvases[0].layers[id].shape ??
        this.listImageShapes[0]
      );
    },
    getBorderRadius({ id }, canvas, banner) {
      return (
        canvas.layers[id].borderRadius ??
        banner.canvases[0].layers[id].borderRadius ??
        0
      );
    },
    getFontSize({ id }, canvas, banner) {
      return (
        canvas.layers[id].fontSize ??
        banner.canvases[0].layers[id].fontSize ??
        16
      );
    },
    getFontColor({ id }, canvas, banner, scheme) {
      const schemeId = scheme || banner.schemes[0].id;
      return (
        canvas.layers[id].schemes[schemeId].fontColor ??
        banner.canvases[0].layers[id].schemes[schemeId].fontColor ??
        '#000000'
      );
    },
    getFontFamily({ id }, canvas, banner) {
      return (
        canvas.layers[id].fontFamily ??
        banner.canvases[0].layers[id].fontFamily ??
        this.listFontFamily[0]
      );
    },
    getFontWeight({ id }, canvas, banner) {
      return (
        canvas.layers[id].fontWeight ??
        banner.canvases[0].layers[id].fontWeight ??
        this.listFontWeight[3]
      );
    },
    getTextAlign({ id }, canvas, banner) {
      return (
        canvas.layers[id].textAlign ??
        banner.canvases[0].layers[id].textAlign ??
        'left'
      );
    },
    getTextVerticalAlign({ id }, canvas, banner) {
      return (
        canvas.layers[id].textVerticalAlign ??
        banner.canvases[0].layers[id].textVerticalAlign ??
        'flex-start'
      );
    },
    getLineHeight({ id }, canvas, banner) {
      return (
        canvas.layers[id].lineHeight ??
        banner.canvases[0].layers[id].lineHeight ??
        110
      );
    },
    getLetterSpacing({ id }, canvas, banner) {
      return (
        canvas.layers[id].letterSpacing ??
        banner.canvases[0].layers[id].letterSpacing ??
        0
      );
    },
    getRotation({ id }, canvas, banner) {
      return (
        canvas.layers[id].rotate ?? banner.canvases[0].layers[id].rotate ?? 0
      );
    },
    getCopyrightType(canvas, banner, scheme) {
      const schemeId = scheme || banner.schemes[0].id;
      return (
        canvas.schemes[schemeId].copyright ||
        banner.canvases[0].schemes[schemeId].copyright ||
        this.copyrightTypes[0]
      );
    },
    getBackgroundStyle(canvas, banner, scheme) {
      const schemeId = scheme || banner.schemes[0].id;
      const background = this.getFile(
        banner,
        canvas.schemes[schemeId]?.background,
      );
      return background.url ? `url(${background.url})` : null;
    },
    getBackgroundWithTextStyle(canvas, banner, scheme) {
      const schemeId = scheme || banner.schemes[0].id;
      const background = this.getFile(
        banner,
        canvas.schemes[schemeId]?.backgroundWithText,
      );
      return background.url ? `url(${background.url})` : null;
    },

    // Set layer properties
    setTitle(layer, $e) {
      layer.title = $e;
    },
    setLeftOffset({ id }, $e) {
      this.canvas.layers[id].left = Number($e.target.value);
    },
    setTopOffset({ id }, $e) {
      this.canvas.layers[id].top = Number($e.target.value);
    },
    setWidth({ id }, $e) {
      this.canvas.layers[id].width = Number($e.target.value);
    },
    setHeight({ id }, $e) {
      this.canvas.layers[id].height = Number($e.target.value);
    },
    setFontFamily({ id }, $e) {
      this.canvas.layers[id].fontFamily = $e;
    },
    setFontWeight({ id }, $e) {
      this.canvas.layers[id].fontWeight = $e;
    },
    setFontSize({ id }, $e) {
      this.canvas.layers[id].fontSize = $e.target.value;
    },
    setFontColor({ id }, $e) {
      const layer = this.canvas.layers[id];
      layer.schemes[this.currentSchemeId].fontColor = $e.hex || $e.target.value;
    },
    setLineHeight({ id }, $e) {
      this.canvas.layers[id].lineHeight = $e.target.value;
    },
    setLetterSpacing({ id }, $e) {
      this.canvas.layers[id].letterSpacing = $e.target.value;
    },
    setTextAlign({ id }, $e) {
      this.canvas.layers[id].textAlign = $e.target.value;
    },
    setTextVerticalAlign({ id }, $e) {
      this.canvas.layers[id].textVerticalAlign = $e.target.value;
    },
    setImageShape({ id }, $e) {
      const layer = this.canvas.layers[id];

      if ($e.value === 'circle') {
        const { width, height } = layer;

        if (width > height) {
          layer.width = height;
        } else {
          layer.height = width;
        }
      }

      nextTick(() => {
        layer.shape = $e;
      });
    },
    setBorderRadius({ id }, $e) {
      this.canvas.layers[id].borderRadius = $e.target.value;
    },
    setRotation({ id }, $e) {
      this.canvas.layers[id].rotate = $e.target.value;
    },
    setUserGroup(id) {
      this.banner.userGroup = this.listUserGroups[id];
    },

    // Manipulate color scheme
    addColorScheme(data, schemesEl) {
      const basicSchemeId = this.schemes[0]?.id;
      const newSchemeId = nanoid();
      this.schemes.push({
        id: newSchemeId,
        ...data,
      });

      if (basicSchemeId) {
        this.canvases.forEach((canvas) => {
          const canvasStyles = JSON.parse(
            JSON.stringify(canvas.schemes[basicSchemeId]),
          );
          canvas.schemes[newSchemeId] = { ...canvasStyles };

          this.layers.forEach((layer) => {
            const layerStyles = JSON.parse(
              JSON.stringify(layer.schemes[basicSchemeId]),
            );
            layer.schemes[newSchemeId] = { ...layerStyles };

            const canvasLayer = canvas.layers[layer.id];
            const canvasLayerStyles = JSON.parse(
              JSON.stringify(canvasLayer.schemes[basicSchemeId]),
            );
            canvasLayer.schemes[newSchemeId] = { ...canvasLayerStyles };
          });
        });
      } else {
        this.canvases.forEach((canvas) => {
          canvas.schemes[newSchemeId] = {
            background: '',
            backgroundWithText: '',
          };
        });
      }

      this.switchColorScheme(newSchemeId);
      if (schemesEl) {
        nextTick(() => {
          schemesEl.scrollTo(schemesEl.scrollWidth, 0);
        });
      }
    },
    updateColorScheme(data) {
      const scheme = this.schemes.find((item) => item.id === data.id);

      scheme.color = data.color;
      scheme.usedColors = data.usedColors;
    },
    switchColorScheme(id) {
      this.currentSchemeId = id;
    },
    selectColorScheme(id) {
      this.selectedColorScheme = this.schemes.find(
        (scheme) => scheme.id === id,
      );
    },
    getColorScheme(id) {
      return this.schemes.find((scheme) => scheme.id === id) || {};
    },

    // Manipulate files
    addFile(file) {
      this.files.push(file);
    },
    deleteFile(id) {
      const usedFiles = [];
      const file = this.files.find((item) => item.id === id);

      if (!file) return;

      // Push every image used
      this.canvases.forEach((canvas) => {
        Object.entries(canvas.schemes).forEach(([, scheme]) => {
          if (scheme.background) {
            usedFiles.push(scheme.background);
          }
          if (scheme.backgroundWithText) {
            usedFiles.push(scheme.backgroundWithText);
          }
        });
      });
      this.layers.forEach((layer) => {
        Object.entries(layer.schemes).forEach(([, scheme]) => {
          if (scheme.image) {
            usedFiles.push(scheme.image);
          }
        });
      });

      // If there are duplicates, it means the image is used multiple times and you can't delete it
      const filteredUsedFiles = usedFiles.filter((item) => item === id);
      if (filteredUsedFiles.length === 0) {
        this.files.splice(this.files.indexOf(file), 1);
      }
    },
    getFile(banner, id) {
      return banner.files.find((item) => item.id === id) || {};
    },

    // Manipulate layer
    addLayer(type) {
      const layerId = nanoid();
      const basicLayer = {
        type,
        id: layerId,
        schemes: {},
      };
      const layer = {
        width: null,
        height: null,
        top: null,
        left: null,
        rotate: null,
        schemes: {},
      };

      switch (type) {
        case 'image':
          this.schemes.forEach((scheme) => {
            basicLayer.schemes[scheme.id] = {
              image: '',
            };
            layer.schemes[scheme.id] = {};
          });
          basicLayer.title = 'Изображение';
          layer.shape = null;
          layer.lockAspectRatio = false;
          break;

        case 'text':
          this.schemes.forEach((scheme) => {
            basicLayer.schemes[scheme.id] = {};
            layer.schemes[scheme.id] = {
              fontColor: null,
            };
          });
          basicLayer._text = 'Ваш текст большой';
          basicLayer._textLimit = '';
          basicLayer.title = '';
          layer.fontFamily = null;
          layer.fontWeight = null;
          layer.fontSize = null;
          layer.lineHeight = null;
          layer.letterSpacing = null;
          layer.textAlign = null;
          layer.textVerticalAlign = null;
          this.addTextLayerGetterSetter(basicLayer);
          break;

        default:
          break;
      }

      this.layers.unshift(basicLayer);
      this.canvases.forEach((canvas) => {
        canvas.layers[layerId] = JSON.parse(JSON.stringify(layer));
      });
      this.activateLayer(basicLayer);
    },
    deleteLayer(layer) {
      const layerIndex = this.layers.findIndex((item) => item.id === layer.id);
      this.layers.splice(layerIndex, 1);
      this.canvases.forEach((canvas) => {
        delete canvas.layers[layer.id];
      });
      this.activateLayer(this.layers[0]);
    },
    positionLayer(left, top) {
      const layer = this.activeLayer;
      this.canvas.layers[layer.id].top = top;
      this.canvas.layers[layer.id].left = left;
    },
    resizeLayer(left, top, width, height) {
      const layer = this.activeLayer;
      this.canvas.layers[layer.id].width = width;
      this.canvas.layers[layer.id].height = height;
      this.canvas.layers[layer.id].top = top;
      this.canvas.layers[layer.id].left = left;
    },
    activateLayer(layer) {
      this.layers.forEach((item) => {
        item.isActive = item.id === layer.id;
      });
    },
    deactivateLayer(layer) {
      layer.isActive = false;
    },
    shiftLayer(layer, direction, isShiftKey) {
      const canvasLayer = this.canvas.layers[layer.id];
      const shiftBy = isShiftKey ? 10 : 1;
      const layerX = this.getLeftOffset(layer);
      const layerY = this.getTopOffset(layer);
      switch (direction) {
        case 'up':
          canvasLayer.top = layerY - shiftBy;
          break;

        case 'right':
          canvasLayer.left = layerX + shiftBy;
          break;

        case 'down':
          canvasLayer.top = layerY + shiftBy;
          break;

        case 'left':
          canvasLayer.left = layerX - shiftBy;
          break;

        default:
          break;
      }
    },

    // Image upload
    updateImage(layer, $event) {
      const scheme = layer.schemes[this.currentSchemeId];
      const oldImageId = scheme.image;
      scheme.image = $event?.id;

      this.addFile($event);
      this.deleteFile(oldImageId);
    },
    updateBackground($event) {
      const scheme = this.canvas.schemes[this.currentSchemeId];
      const oldImageId = scheme.background;
      scheme.background = $event?.id;

      this.addFile($event);
      this.deleteFile(oldImageId);
    },
    updateBackgroundWithText($event) {
      const scheme = this.canvas.schemes[this.currentSchemeId];
      const oldImageId = scheme.backgroundWithText;
      scheme.backgroundWithText = $event?.id;

      this.addFile($event);
      this.deleteFile(oldImageId);
    },

    resetState() {
      this.currentSchemeId = '';
      this.currentCanvasId = 0;
      this.banner.status = '';
      this.banner.error = '';

      const banner = this.banner.data;

      banner.conversion = '';
      banner.userGroup = null;
      banner.category = null;
      banner.layers = [];
      banner.schemes = [];
      banner.files = [];

      banner.canvases.forEach((canvas) => {
        canvas.layers = {};
        canvas.schemes = {};
      });
    },
  },
});
